.header{
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
    margin: 5px 0;
}

.logo{
    width: 150px;
}

.nav-items > ul{
    display: flex;
    padding: 0px 20px;
    font-size: 24px;
}

.nav-items > ul >li{
    padding: 10px;
    margin: 10px;
    list-style: none;
}

.res-container{
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    margin: 5px 0;
}

.res-card{
    padding: 5px;
    width: 200px;
    /* height: 400px; */
    /* border: 1px solid black; */
    background-color: rgb(226, 223, 223);
}

.res-card:hover{
    border: 1px solid black;
    cursor: pointer;
}

.filter-btn{
    /* margin: 0 5px 0 0 ; */
    cursor: pointer;
}

.res-logo{
    width: 100%;
}

.shimmer-container{
    display: flex;
    flex-wrap: wrap;
}

.shimmer-card{
    margin: 20px;
    height: 400px;
    width: 200px;
    background-color: #f0f0f0;
}

.login{
    padding: 0 20px;
    cursor: pointer;
}

.filter{
    display: flex;
}
.search{
    margin: 0 5px;
}
